        work:['Ban co th cam thy rt ro cm gicbi troi buc vo mt cng vic ma ban khng thich nhng khng th thy c cach thc ro rang no  ri bo vi cm gic c am bao vi cng vic . Hay xem xet liu vic anh i vy co ang khng; thc cht cua vn  nh ngay lam, tui tac, vic nhiu hay it, tr cp, v.v . co thc s c am bao hay khng? Nu ban chon  lai, hay nhn thc rng y la mt la chon ma ban ang a ra,  ch khng co gi hay ai khc ap t ban. Nu ban cam thy vic anh i nay khng ang, hay dp tt nhng cam giac nay va nghi v iu ban co th lam  thay i hoan canh. Ban se khng bi troi buc tr khi ban cho phep ban thn minh bi troi buc.',' y la thi im quan trong  nhn ra khi ban rut la The Devil ngc rng bt ky cng vic nao khng inh dai lu va am bao trong ngay hm nay va giai oan nay u dng nh khng am bao mai mai. y khng hn la mt iu xu, thng tin va kin thc mi la sc manh; va vic bit rng ban co la chon la mt ch da tinh thn manh me thay vi cam thy nh ban phai  trong mt hoan canh mai mai vi ban nghi no c bao h va am bao. Co qua nhiu ngi trong nhng nm gn y ang thy nhng khoan thng va tr cp ha hen bi tc i mt cach nhanh chong. Vng, ban phai la mt phn cua chinh ban, s am bao n t bn trong, khng phai t th gii bn ngoai. Nu ban ang ri vo mt hoan canh khin bn khng cam thy hai long v cng vic vi tinh am bao v an ton m n em li, th hay nhn thc rng y la mt la chon ma ban ang a ra, khng co gi hay bt c ai ang thuc ep ban. Nu ban khng cam thy vic anh i ng vi cm gic khng hi lng ca bn thn, hay bt u hay dp tt nhng cam giac nay va nghi v iu ban co th lam  thay i hoan canh. Ban se khng bi troi buc tr khi ban cho phep ban thn minh bi troi buc. Lun lun co la chon theo nhiu cach khac nhau danh cho ban.'],
        love: [' Nu ban ang trong mt mi quan h tinh cam lu dai, th mt trong hai bn hoc ca hai co th ang trong hoan canh nh th cam thy nh ang bi troi buc. Nu ban ang co nhng du hiu nh vy, hay i noi ro chuyn o nu ban mun gia vng mi quan h. Chung ta lun co la chon. Hay chu ng  chuyn thn ky xay ra. Nu ban ang tim kim tinh yu cua minh, y co th la tin hiu rng ban ang tim kim trong v vong, mt thai  ro rang y nhng i tng co-kha-nng-yu-ban ra xa. Nu ban ri vao trng hp o, hay xem xet danh mt khoang thi gian ngn  thoat khoi mi quan h o   tim kim va hiu ban thn minh tt hn. Hay quay lai cuc tim kim cua ban khi ban cam thy khng con cp bach na.','La bai The Devil ngc co th la mt du hiu thn thin rng vn  co th ang p u mt cach m h nu ban ang trong mt mi quan h tinh cam lu dai. Mt bn hoc ca hai co th ang bt u cam thy nh ang bi troi buc trong hoan canh. Thi im nay se n sm khi ban co nhu cu  cp n, nu ban mun cu gi mi quan h. Tuy nhin, nghia ngc cua la bai nay khng  cp nhiu i vi mi quan h tinh cam lu dai so vi nghia xui. Nu ban ang tim kim tinh yu ma rut c la bai nay, hay a ra mt s cn nhc xem liu ban co cam thy qua go bo trong tinh trang c n va mun lam ht sc minh  em lai s thanh than cho tinh canh cua minh hay khng. Mt s phat tit mun co c tinh yu se khin cho nhng i tng tim nng gn ban chay i mt. Nhng ngi eo bam va liu linh thng khng hp dn vi nhng i tng ang hoang. Hay am bao ban khng thuc dang nh vy. Hay xem xet dng hen ho mt thi gian.'],
        finance:['Moi th co th hoc s rt cng thng trong tng lai gn. Hay c ng cau gt vi hoan canh cua minh, thay vao o hy giai quyt trong binh tinh va sang sut. Hay i tng bc mt. Nu ban cn giup  v tai chinh, ng ngn ngai hoc qua kiu hanh  a ra yu cu. S giup  lun sn sang vi ban. Nu ban cam thy tnh hnh ti chnh ang d da  hin ti th y vn la thi im nhc nh ban tit ch v tai chinh. Hay tit kim  phong nhng ngay gian kho!','Ging vi nghia xui, la The Devil ngc cho thy tnh hnh ti chnh co th rt cng thng trong tng lai gn. Ban cn c gng khng tr nn bc tc hay cu bn vi hoan canh cua minh, thay vao o hy giai quyt mt cach binh tinh va sang sut. Hay tin hanh tng bc mt. Cho du ban co trong tinh canh nao i na th mt iu chc chn rng no se khng keo dai mai mai. Nu ban cn s giup  v tai chinh, ng ngai hoc qua t cao  a ra  nghi. S giup  lun sn sang xut hin. Khi ban rut la bai nay trong khi vn  tin bac cua minh vn tao thanh dong chay tt, thi y vn la thi im canh bao ban v vn  tai chinh. Hay gi tin phong khi co chuyn!'], 
