        spirit: ['Vic gi mt quan nim tich cc la iu v cung quan trong trong luc nay. Nhng li xac tin tich cc rt quan trong va se co li cho ban. Hay nhin nhng ngi xung quanh ban. Liu ho la ngi lac quan, tich cc gia moi ngi, hay ho la nhng ngi thich chi trich thai qua, nhng ngi i bt li ngi khac khin cho ban phin mun? Gieo suy nghi, gt hanh ng. Cuc i qua ngn ngui, ma tm trang lai d ly truyn cho nhau.','Vi la The Devil ngc, thai  cua ban va quan nim tich cc vn rt quan trong. c bit nu ban ang co kho khn trong vic gi mt thai  tt, hay xem xet nhng ngi xung quanh va ai thc s la ban cua ban. Hay gi nhng ngi tich cc  canh ban, hoc liu ban co bi vy quanh bi nhng ngi hay chi trich thai qua vn hay than phin va soi moi ban  nhng im ban cha lam tt c hay khng? Gieo suy nghi, gt hanh ng. Cuc i qua ngn ngui, ma tm trang lai d ly truyn cho nhau. La bai nay mt ln na nhc ban rng khng vn  gi trng ging ve b ngoai, ban khng thc s bi troi buc. Ngay ca khi ban thc s ang bi cm tu, ho cung khng th ep buc tinh thn, linh hn, hay tri oc ban c. Hay s dung s t do ma ban co.']
