        work:['Nng lng vao thi im c khuynh hng dn ti cc tranh ci hay xung t; hay c gi cho tinh khi ban trong tm kim soat. Nhng xoay chuyn ao ngc bt cht co th xay ra luc nay. y khng phai la mt du hiu khin ban se phai hoang s. Tuy nhin, n li l mt du hiu canh bao trc  ban chun bi. Ngay ca khi ban nghi rng minh ang co mt cng vic hoc mt ngun thu nhp n inh nht th gii, ban se lam gi nu tt ca chung bin mt vao ngay mai? Ni nao ban co th chuyn n  lam vic ngay lp tc nu ban bi bt buc phai ra i? Vic suy nghi thng sut ngay t by gi, khi ban cha trong tinh trang khung hoang, co th la li th ln vi ban phng khi ban bi tut dc. Nu ban cm thy chn ghet cng vic hin tai, hay suy nghi nghim tuc  quyt inh thay i. Ban ang c hng hanh phuc m!','Khi la bai The Tower xut hin ngc, nhng ngi xung quanh ban thng co th ang gp phi tnh trng stress, tranh ci hay xung t, hiu lm vn c th se dn n hu qua kho lng. iu quan trong l ban khng nn hanh x theo hng ch k ca nhn, va ng  ban thn minh dinh liu vao nhng xung t khng cn thit hoc  cho cai ti chim linh ban. Vi la bai The Tower ngc, vic suy nghi ky trc khi noi la c bit cn thit.'],
        love: ['y co th la (nhng khng nht thit) mt du hiu cua mt mi quan h co kha nng kt thuc. Nu ban ang trong mt mi quan h ma ban thc s khng mun dn n kt thuc, thi y la thi im quan trong danh cho vic kim soat mi nguy va danh cho vic noi chuyn ro rang vi nhau. Hay tim hiu xem i tng cua ban ang cam thy nh th nao. ng suy din hay t a ra gi thuyt, m hay tim hiu ro rang.','Khi ban rut c la The Tower ngc, nhin chung moi th lin quan n mi quan h yu ng se khng khc nghit nh khi rut la bai  chiu xui. Tuy nhin, y vn la mt tin hiu rng mi quan h cn c u t va trao i vi nhau nhiu hn nu ban mun no tn tai. Hay suy nghi tich cc va ng tp trung vao vic thay i i tng cua ban, hay xem xet cach ban co th ong gop va cai thin phn cua chinh ban trong mi quan h. Tin tng bc mt va ng anh mt nhit huyt cua minh.'],
        finance:['y khng phai la thi im danh cho nhng cuc u t mao him, anh cc, hay da vao x s  giai quyt cac vn  cua ban. Ngay ca khi ban bit nhng tin tc khng tt lanh my, th cng hay tim hiu chinh xac xem ban ang phai i mt vi vn  tai chinh no. No co th khng n mc t nh ban nghi u. Tuy nhin, tranh nhng rc ri vn l tt hn c. Hay giai quyt vn  theo mt cach n gian.','Cung ging nh khi The Tower  vi tri xui, y vn khng phai thi im  u t mao him, hay nhng tro may rui. Nhin chung, trong hoan canh nay, la The Tower ngc cho thy thc t rng vn  tai chinh cua ban co th khng ln nh ban lo s. D vy khng co gi tt hn la tranh nhng rc ri. Hay giai quyt nhng vn  theo cach n gian nht. Nu ban cn giup  giai quyt v tai chinh, ng ngn ngai a ra yu cu.'], 
        health:['Vic chu y nhng gi ban ang lam rt cp thit vao luc nay. Hay cn thn. Thn kinh ban co th bi cng thng, hay lam bt c iu gi cn thit  ban binh tinh lai va  trong trang thai n inh. Vic tp th duc co th giup ich cho ban. Nu ban bit c nhng gi ban ang lam (va bit c v chng di ng cua minh, hay nhng ph chun cua bac sy) th hay cn nhc vic th th gian bng thao mc. Hay cn thn vi thuc va thc ung co cn trong thi im nay, nu ban ang dung chung.','Cung vy, la The Tower ngc cho thy nhng vn  nhe hn so vi khi no  vi tri xui. Trong mt vai ngay ti ban nn cn thn va chu y nhng gi minh ang lam nu chung  trong hoan canh co th gy nguy him, nh lai xe, ct co. Hay binh tinh va bit rng chuyn nay ri cung qua.'],
        spirit: ['y cung la thi im  gi mt thai  tich cc nht co th. Nhng hiu bit va nhn thc mi me co th n vi ban trong chp mt.','Cung ging nh y nghia xui, la The Tower ngc cho chung ta bit rng y la thi im  gi thai  tich cc nht co th, bt k co chuyn ang xay ra quanh ban, hay lun tin tng vao s phu h danh cho ban, va ng t mt minh lam nhng cng vic vn khng phai vic danh cho ca nhn. Nhng hiu bit va nhn thc su sc mi co th n vi ban trong nhay mt.']
