        work:['Khng may l mi vic c th khng r rng mt cht no. Hy hi v tp lng nghe ch ng  m bo rng bn v ng nghip hiu nhau hon ton. Mt ln na, s kin nhn l cn thit. Hy bnh tnh, v c th l khi bn cm thy nh ang b cng kch th hy tin vo ng nghip hoc ngi gim st ca bn. S tht c th khng t nh bn ngh u.','hi The Moon xut hin  v tr ngc, mi vic c th khng r rng mt cht no. iu tn thng nht l khi bn cm thy nh mnh ang nm rt r rng v mt vic no  th tht ra li l khng phi. Quan trng l bn hy t cu hi,  t bit l i vi sp hay cp trn ca mnh, v tp lng nghe ch ng  m bo rng bn v ng nghip hiu nhau hon ton. Hy kin nhn v bnh tnh, v nht l khi bn cm thy nh b cng kch, th hy tin tng vo ng nghip hoc gim st ca bn, v s tht c th khng t nh bn ngh u. ng i hi nhng thay i ln t cng vic trc mt; y c l l cha phi lc, tr khi bn ang tm kim mt v tr mi.'],
