        finance:['Nu bn ang ch i nhng quyt nh v mt ti chnh do ngi khc thc hin, th s xut hin ca l bi ny c th ng  rng bn s phi ch i lu hn na. y khng phi l lc  ra nhng quyt nh kinh doanh nu khng c nhng dn chng tht c th v chc chn.','Khi The Moon xut hin  v tr ngc trong trng hp nhng cu hi lin quan n ti chnh, th r rng l bn khng c tt c nhng thng tin cn thit lin quan n vn  ny, cho d bn ngh l bn c. Nu bn ang gp phi nhng kh khn v ti chnh hoc ang nh k hp ng no , hy i tm nhng li khuyn t cc chuyn gia trc khi k hoc thc hin nhng thay i ln trong vn  ti chnh. Khng phi ch c bn b thiu i s r rng, khi l bi ny xut hin, nhng ngi xung quanh bn cng c th ang lc hng.'], 
